.text-white {
    color: #fff !important;
}

.color-bg {
    background: #535164 !important;
}

.color-text {
    color: #535164 !important;
}

.options-list {}

.options-item {
    font-family: 'MyWebFont';
    text-transform: uppercase;
    background: #535164;
    color: #F7F8FD !important;
    text-decoration: none;
    padding: 20px 76px;
    border-radius: 60px;
    font-size: 15px;
    margin-bottom: 10px;
    text-align: center;
}

.carbon-text{
    text-align: center;margin-top: 1em;
}

.temp-circle{
    margin-left: 50px;
}

.flow-co2{
    margin-left: 5em;transform: rotate(-65deg);
}

.co2-circle{
    margin-left: 0em;
}

.flow-flight{
    margin-left: 5em;transform: rotate(255deg);
}

.flight-circle{
    margin-left: 3em;
}

.airline-container{
    display: flex; flex-direction: column;justify-content: space-between; align-items: center;padding: 1em;
}

.flow-temp{
    margin-left: 9em;transform: rotate(-60deg);
}

.r45 {
    transform: rotate(-45deg);
}

.select-dropdown {
    width: 80vw;
    border-radius: 50px;
    padding: 10px;
    background: #F7F8FD;
}

.send {
    position: fixed;
    bottom: 5px;
}

.discussions {
    display: flex;
    flex-direction: column-reverse !important;
    justify-content: end;
    height: 100%;
    padding-bottom: 60px;
}

.flow {
    height: 100px;background-color: #F7F8FD;
}

.flow-brand{
    margin-right: 0%;
}

.flow-container {
    height:80%;
}

.flow-text {
    margin-top: 2em !important;
}